import Vue from 'vue'
import Vuex from 'vuex'
import actions from './actions'
import mutations from './mutations'
import params from '../config/params'

Vue.use(Vuex);

const state = {
	config:params,
	sys_name:'360',
	show_tabbar:true,
	show_topbar:true,
	login_logo:'images/login_logo.jpg',
	copyright:'&copy;2017 ygwo.net',
	openid:'',
	isLoading:false,
	direction:'forward',
	show_footer:true,
	bids: [],
	ui_config: {
		
	},
	lesson_standard_file: [],
	user:{
		openid:'',
		login_time:0,
		login_server_time:0,
		expire_time:0,
		token:'',
		info:null,
		default_rid: 1,
		notice: {
			today_attendance_num:0,
			today_broadcast_num:0,
			today_course_num:0,
			today_review_num:0
		}
	},
	jssdk:{
		ready:false,
		request_time:0,
		expire:7200,
		debug: false, // ,apialertpclogpc
    	appId: '', // 
    	timestamp:0 , // 
    	nonceStr: '', // 
    	signature: '',// 1
    	jsApiList: [] // JSJS2
	},
	session:{
	},
	gvars:{

	},
	maps: {
		
	},
	ws_host:'',
	base_url:'',
	socket: {
      isConnected: false,
      isUserOnline:false,
      uid:0,
      fd:0,
      message: '',
      event:'',
      data:{},
      user_error:'',
      error:false
    },

    chat_upload:{
    	popup:false,
    	started:false,
    	path:'',
    	status:0,
    	msg:'',
    	data:{
    		file_type:'',
    		file_url:'',
    		file_size:''
    	}
    }
}

const getters = {
	isLogin(state){
		let cur_time = (new Date()).getTime() / 1000
		let expired_time = state.user.login_time + state.user.expire_time
		return state.user.login_time > 0 && expired_time > cur_time
	},
	isLock(){
		let islock = Vue.localStorage.get('islock')
		return String(islock) === 'true'
	},
	getGlobalVar:(state,getter)=>(name)=>{
		return state.gvars[name]
	},
	jssdkReady(state){
		return state.jssdk.ready
	}
}


export default new Vuex.Store({
	state,
	getters,
	actions,
	mutations
})


