
import {_} from '@/libs/util'
import util from '@/libs/util'
import common from '@/libs/common.mixin'
import wxjssdk from '@/libs/wxjssdk.mixin'
import CellContent from '@/views/home/comment/cell-content.vue'
import Card from 'vux/src/components/card/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Rater from 'vux/src/components/rater/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import MyRater from 'c%/MyRater.vue'

export default{
mixins: [common,wxjssdk],
components: {
Card,
Cell,
Rater,
Group,
XButton,
Flexbox,
MyRater,
CellContent,
FlexboxItem,
},
data () {
return {
data: {},
students: [],
ready: false
}
},
mounted () {
this.init_data()
},
methods: {
init_data () {
this.$rest(this.data_uri).get()
.success(data=>{
this.data = data
this.ready = true
this.setShare()
})
},
scoreLabel (index) {
let scoreFields = this.currentTpl?this.currentTpl.setting.score_fields:{}
if(scoreFields){
return scoreFields['score'+(index>0?index:'')].label
}else{
return ''
}
},
setShare () {
let title = '',
day = this.$filter('int_date')(this.data.int_day),
start_hour = this.$filter('int_hour')(this.data.int_start_hour),
end_hour = this.$filter('int_hour')(this.data.int_end_hour),
ename = this.data.employee.ename

title = util.sprintf('%s%s%s %s~%s',ename,this.reviewObject,day,start_hour,end_hour)

this.setWxShare(title)
.then(()=>{
alert('')
})
}
},
computed: {
reviewObject () {
let reviewObject = ''
if(this.data){
if(this.data.lesson_type===0){
if(this.data.one_class){
reviewObject = this.data.one_class.class_name
}
}else if(this.data.lesson_type===1){
reviewObject = ''
}else{
reviewObject = ''
}
}

return reviewObject
},
title () {
let result = ''

return result
},
data_uri () {
return 'share/review?rvw_id='+this.$route.params.id
},
currentTpl () {
let obj = {}
if(this.data.rts_id==0)
{
obj.setting = this.data.review_tpl_setting
}else{
obj = this.data.review_tpl_setting
}
return obj
},
content_fields () {
let result = {},
fields = this.currentTpl?this.currentTpl.setting.common_fields:{}

for(let s in fields){
let obj = fields[s]
if(obj.enable==1){
result[s] = {}
result[s].label = obj.label
result[s].value = obj.tpl
result[s].student_view = obj.student_view
}
}

return result
},
scores () {
let result = {},
fields = this.currentTpl?this.currentTpl.setting.score_fields:{}
for(let s in fields){
let obj = fields[s]
if(obj.enable==1){
result[s] = {}
result[s].label = obj.label
result[s].value = obj.default
}
}

return result
}
}

}



