import MeScroll from './mescroll/mescroll.m'
import {_} from './util'

export default{
	bind: function (element, binding,vnode) {
		let refs = 'dataList',
			wrapId = vnode.context.$refs[refs]
			
		if(!_.isEmpty(binding.modifiers)){
			refs = 'dataList'+binding.value
			wrapId = vnode.context.$refs[refs][0]
		}
		
		//dommescrollmescrollbug
		vnode.context.$nextTick(()=>{
			vnode.context.mescroll = new MeScroll(element, { //vuemountedmescroll,id
				up: {
					callback: vnode.context.upCallback, //
					//,
					use: true,
					isBoth: false,
					noMoreSize: 1,
					isBounce: false, //ios,(,): http://www.mescroll.com/qa.html#q10
					page:{
						num: vnode.context.pageIndex,
						size: vnode.context.pageSize
					}, //8,10
					toTop:{ //
						src : "/static/img/student/back-top.png", //1000px,offset
						//html: null, //html,null; src,src
						//offset : 1000
					},
					empty:{ //
						warpId: wrapId,
						icon : "/static/img/student/nodata.png" , 
					  	tip : vnode.context.noDataText||",~" , 
					  	btntext : "  " , 
					  	btnClick : vnode.context.downCallback
					},
					htmlNodata: '<p class="upwarp-nodata">--  --</p>',
				},
				down: {
					callback: vnode.context.downCallback,				
				}			
			});
		})
	},
	unbind: function (el, binding, vnode){		
		vnode.context.mescroll.destroy()
	}
}



