function first_week_begin_day(year) {
  var tempdate = new Date(year, 0, 1);
  var temp = tempdate.getDay();
  if (temp == 1) {
    return tempdate;
  }
  temp = temp == 0 ? 7 : temp;
  tempdate = tempdate.setDate(tempdate.getDate() + (8 - temp));
  return new Date(tempdate);
}

/**
 * 
 * @param  {[type]} year [description]
 * @param  {Number} week [description]
 * @return {[type]}      [description]
 */
function weekdate(year, week) {
  var weekday = {
    start: null,
    end: null
  };
  var first_day = first_week_begin_day(year);
  var time = (week - 1) * 7 * 24 * 3600000;
  var week_time = 6 * 24 * 3600000;
  var start_day = first_day;

  start_day.setTime(first_day.valueOf() + time);
  weekday['start'] = start_day;
  weekday['end'] = new Date(start_day.valueOf() + week_time);
  console.log('year:' + year + ',week:' + week);
  console.log(weekday);
  return weekday;
}

/**
 * 
 * @param  {[type]} year [description]
 * @param  {Number} week [description]
 * @return {[type]}      [description]
 */
function week_date_array(year, week) {
  week = week || 1;
  var dto = weekdate(year, week);
  var arr = [];
  var day_time = 24 * 3600000;
  var start_date = dto.start;
  for (var i = 0; i < 7; i++) {
    arr.push(start_date);
    start_date = new Date(start_date.valueOf() + day_time);
  }
  return arr;
}

