
import Vue from 'vue'
import moment from 'moment'
import util,{_} from '@/libs/util'
import common from '@/libs/common.mixin'
import wxjssdk from '@/libs/wxjssdk.mixin'
import DatetimePlugin from 'vux/src/plugins/datetime/index.js'
import Msg from 'vux/src/components/msg/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'


Vue.use(DatetimePlugin)

const emptyObject = {
bid: 0,
mc_id: 0,
name: '',
tel: '',
email: '',
sex: 1,
birth_time: 0,
grade: '',
remark: '',
is_valid: 0,
qr_eid: 0,
assigned_eid: 0,
recommend_reward_note: ''
}

export default{
mixins: [ common, wxjssdk ],
components: {
Msg,
Divider,
XButton
},
data () {
return {
icon: 'success',
info: util.copy(emptyObject),
showResult: false,
config: {},
msg: {
icon:'success',
title: '',
description: '!'
},
buttons: [{
type: 'default',
text: '',
onClick:  this.back.bind(this)
}]
}
},
mounted () {
this.init_config()
},
computed: {
enableFieldsList () {
let result = []
if(this.config.fields && this.config.fields.length > 0){
result = this.config.fields.filter(f=>f.enable)
}
return result
},
logo () {
let ui = window.CLIENT_PARAMS,
result = '/static/img/logo.png'
if(!_.isEmpty(ui)&&!_.isEmpty(ui.mobile)&&ui.mobile.logo!=''){
result = ui.mobile.logo
}
if(this.config.logo){
result = this.config.logo
}
return result
}
},
methods: {
setMsg (icon,title,description) {
this.msg.title = title
this.msg.description = description
this.msg.icon = icon
},
ok () {
if(/^\s*$/.test(this.info.name)){
this.toast('','warn')
return
}
if(/^\s*$/.test(this.info.tel)){
this.toast('','warn')
return
}
if(!/^1\d{10}|\d{3}-\d{8}|\d{4}-\d{7}$/.test(this.info.tel)){
this.toast('','warn')
return
}

this.info.bid = this.$route.query.bid||0
this.info.mc_id = this.$route.query.mc_id||0
this.info.qr_eid = this.$route.query.eid||0
this.info.assigned_eid = this.$route.query.eid||0

this.showLoading()

this.$rest('open/qr_clue').post(this.info)
.success(data=>{
this.hideLoading()
this.setMsg('success',this.config.msg.title,this.config.msg.description)
this.showResult = true
})
.error(response=>{
this.hideLoading()
this.setMsg('warn','',response.body.message||'~')
this.showResult = true
})


},
showPlugin () {
let $this = this
this.$vux.datetime.show({
cancelText: '',
confirmText: '',
format: 'YYYY-MM-DD',
value: moment(new Date()).format('YYYY-MM-DD'),
endDate: moment(new Date()).subtract(1,'years').format('YYYY-MM-DD'),
onConfirm (val) {
$this.info.birth_time = val
},
onShow () {
console.log('plugin show')
},
onHide () {
console.log('plugin hide')
}
})
},
back () {
this.info = util.copy(emptyObject)
this.setMsg('success',this.config.msg.title,this.config.msg.description)
this.showResult = false
},
init_config () {
this.init_qr_config()
.then(resolve=>{

},reject=>{
this.$rest('/sapi/open/global/?name=configs')
.get()
.success(data=>{
this.config = data.qrsign
}).error(response=>{
this.toast(response.body.message||'','warn')
})
})
},
init_qr_config () {
return new Promise((resolve,reject)=>{
this.$rest(`open/qr_config?mc_id=${this.$route.query.mc_id}`)
.get()
.success(data=>{
if(!_.isEmpty(data)){
this.config = data
resolve()
}else{
reject()
}
}).error(response=>{
reject()
this.toast(response.body.message||'~','warn')
})
})
}
}
}



