
import common from '@/libs/common.mixin'
import MyRater from 'c%/MyRater.vue'
import Popup from 'vux/src/components/popup/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XButton from 'vux/src/components/x-button/index.vue'


export default{
mixins: [common],
components: {
Cell,
Popup,
MyRater,
Flexbox,
XButton,
XTextarea,
FlexboxItem,
},
props: {
readonly:{
type: Boolean,
default: false
},
value:{
type: String,
default: ''
},
scores: {
type: Object,
default: () => {
return {}
}
},
row: {
type: Object,
default: () => {
return {}
}
}
},
data () {
return {
popupShow: false,
model: this.value
}
},
computed: {
activeStar () {
return `<img slot="active" src="/static/img/student/star-sel.png" style="width:20px">`
},
defaultStar () {
return `<img slot="default" src="/static/img/student/star-nor.png" style="width:20px">`
},
},
methods: {
close () {
this.popupShow = false
},
show () {
if(!this.readonly){
this.popupShow = true
}
},
ok () {
this.$emit('input',this.model)
this.close()
}
}

}



