import util from './util'
import {_} from './util'
import moment from 'moment'

function int_day_to_date(int_day){
	let int_day_str = int_day.toString();
    let y = parseInt(int_day_str.substr(0,4),10),
        m = parseInt(int_day_str.substr(4,2),10)-1,
        d = parseInt(int_day_str.substr(6,2),10),
        dt = new Date(y,m,d)
        dt.setHours(0,0,0,0)
    return dt;
}

function broadcast(componentName, eventName, params) {
    this.$children.forEach(child => {
        const name = child.$options.name;

        if (name === componentName) {
            child.$emit.apply(child, [eventName].concat(params));
        } else {
            // todo  params  undefined
            broadcast.apply(child, [componentName, eventName].concat([params]));
        }
    });
}

export default {
    data(){
        return {
            action :'index',
            error:'',
            data: [],
            msg:{
                title:'',
                description:'',
                buttons:[],
                icon:'success' //success, warn, info, waiting
            },
            pageIndex: 1,
            pageSize: 10,
            busy: false,
            loading: false,
            isloadAll: false,
            uploadList: [],
            mescroll: null,
            previewShow: false,
            loadingInterval: null,
            options: {		//
		        getThumbBoundsFn (index) {
		          // find thumbnail element
		          let thumbnail = document.querySelectorAll('.previewer-img')[index]
		          
		          // get window scroll Y
		          let pageYScroll = window.pageYOffset || document.documentElement.scrollTop
		          // optionally get horizontal scroll
		          // get position of element relative to viewport
		          let rect = thumbnail.getBoundingClientRect()
		          // w = width
		          return {x: rect.left, y: rect.top + pageYScroll, w: rect.width}
		        }
	      	}
        }
    },
	filters:{
		int_date(day_int){
			let s = day_int.toString(),
                t = [],
                l = s.length;
            if (l == 8) {
                t.push(s.substr(0, 4));
                t.push(s.substr(4, 2));
                t.push(s.substr(6, 2));
            } else if (l == 6) {
                for (let i = 0; i < l; i++) {
                    t.push(s.substr(i, 2));
                    i++;
                }
            }
            return t.join('-');
		},
		age (time) {
			return util.age(time)
		},
		format_int_day(day_str){
            return day_str.toString().replace(/[^\d]+/g,'')
        },
		int_week(day_int){
			let t = ['', '', '', '', '', '', ''];
            let dt = int_day_to_date(day_int);
            return t[dt.getDay()];
		},
		int_hour(hour_int){
			let s = hour_int.toString(),
                t = [],
                l = s.length;
            switch(l){
                case 0:
                    s = '0000';
                    break;
                case 1:
                    s = '000'+s;
                    break;
                case 2:
                    s = '00'+s;
                    break;
                case 3:
                    s = '0'+s;
                    break;
                default:
                    s = s;
            }            
           	t.push(s.substr(0,2))
           	t.push(s.substr(2,2))
            return t.join(':');
		}
	},
    methods: {
    	preview ({list,index}) {
			list = list||[]
			this.list = []
			if(list.length){
				list.forEach(l=>{
					this.list.push({
						src: l.file_url
					})
				})
			}
			this.previewShow = true
			setTimeout(()=>{
				this.$refs.previewer.show(index)					
			},100)
		},
		close () {
			this.previewShow = false
		},
    	imgLoadError (e) {
    		e.target.src = this.defaultImg$
    	},
    	dr(){
            return this.$store.state.layout.$refs['dr']
        },
        branch() {
    	  	return this.$store.state.layout.$refs['branch']
        },
        page(action){
            this.action = action
        },
        _msg(title,content,icon,first_btn,second_btn){
            this.msg.title = title
            this.msg.description = content
            this.msg.icon = icon
            if(_.isFunction(first_btn)){
                this.msg.buttons[0] = {
                    type:'primary',
                    text:'',
                    onClick:first_btn
                }
            }else{
                this.msg.buttons[0] = first_btn
            }
           
            if(second_btn){
                if(_.isFunction(second_btn)){
                    this.msg.buttons[1] = {
                        type:'primary',
                        text:'',
                        onClick:second_btn
                    }
                }else{
                    this.msg.buttons[1] = second_btn 
                }
               
            }
            this.page('msg')
        },        
        success(title,content,first_btn,second_btn){
            this._msg(title,content,'success',first_btn,second_btn)
        },
        error_msg(title,content,first_btn,second_btn){
            this._msg(title,content,'warn',first_btn,second_btn)
        },
        error_tip(tip){
            this.error = tip
        },
        clear_error(){
            this.error = ''
        },
        dispatch(componentName, eventName, params) {
            let parent = this.$parent || this.$root;
            let name = parent.$options.name;
            while (parent && (!name || name !== componentName)) {
                parent = parent.$parent;

                if (parent) {
                    name = parent.$options.name;
                }
            }
            if (parent) {
                parent.$emit.apply(parent, [eventName].concat(params));
            }
        },
        broadcast(componentName, eventName, params) {
            broadcast.call(this, componentName, eventName, params);
        },        
        dicts (type) {
        	let gvars = this.$store.state.gvars
        	if(gvars.dicts){
        		let dicts = gvars.dicts[type]
        		if(dicts&&dicts.length>0){
	        		return dicts
	        	}
        	}        	
        	return []
        },        
        gvars (type) {
        	let gvars = this.$store.state.gvars
        	return gvars[type]||[]
        },
        //id:idnamename:name
        labelDicts (id,field) {
			let [obj,result] = [[],'']
			if(this.$store.state.gvars.dicts){
				obj = this.$store.state.gvars.dicts[field]
			}
			if(!_.isEmpty(obj)){
				if(field=='grade'||field=='season'){
					if(result = obj.find(o=>o.name==id)){
						result = obj.find(o=>o.name==id).title						
					}
				}
				
				if(obj.find(o=>o.did===id)){
					result = obj.find(o=>o.did===id).title	
				}					
			}
			return result
		},		
        downCallback () {
			this.pageIndex = 0
			if(this.mescroll){
				this.mescroll.resetUpScroll()				
			}
		},
		upCallback () {
            let get_param = {}
        	this.pageIndex++
            get_param.page = this.pageIndex
            get_param.pagesize = this.pageSize
            this.showLoading()
            
            this.hook_get_param(get_param)
            for(let o in get_param){
                if(get_param[o] === null){
                    delete get_param[o]
                }
            }
            
            this.$http.get(this.data_uri,{params:get_param})
            .then(response=>{	                
                this.deal_data(response)
                
                this.loadFinished(response)
                this.hideLoading()
            }).catch(response=>{
            	this.hideLoading()
            	this.mescroll.endErr()
            	this.toast(response.body.message,'text')
            })
        },
        hook_get_param (get_param) {
        	
        },
        deal_data (response) {
        	let list = response.body.data.list,	
            	total = response.body.data.total,
            	curLength = 0
            	
        	if(this.pageIndex===1){
        		this.data = []
        	}        	

    		this.data = this.data.concat(list)
    		
    		this.mescroll.endBySize(list.length, total)
        },
        loadFinished (response) {
        	
        },
        loadMore () {
        	if(this.isloadAll){ return false }
            let get_param = {}
        	this.pageIndex++
            get_param.page = this.pageIndex
            get_param.pagesize = this.pageSize
            this.loading = true
            this.busy = true
            this.$http.get(this.data_uri,{params:get_param})
            .then(response=>{
                let list = response.body.data.list
                if(list.length == 0 || list.length < this.pageSize){
                    this.isloadAll = true
                }
                this.data = this.data.concat(list)
            	setTimeout(()=>{
            		this.loading = false
            		this.busy = false	
            	},1000)            	
            }).catch(response=>{
            	this.loading = false
            	this.toast(repsonse.body.message,'text')
            })
        },
        getAge (birth_time) {
        	if(birth_time!=0){
        		return parseInt(moment(new Date()).format('YYYY')) - parseInt(moment(birth_time).format('YYYY')) + ''        		
        	}
        	else{
        		return ''
        	}
		},
        toast (msg,type) {
        	//type : success, warn, cancel, text
        	this.$vux.toast.show({
        	  type: type || 'text',
			  text: msg
			})
        },
        alert (title,msg,autoHidden) {
        	return new Promise((resolve,reject)=>{
        		this.$vux.alert.show({
				  title: title || '',
				  content: msg,
				  onHide() {
				  	resolve()
				  }
				})
		    	if(autoHidden){
		    		setTimeout(()=>{
						this.$vux.alert.hide()
						resolve()
					},3000)
		    	}
        	})	    	
        },
        showLoading () {
	        this.$vux.loading.show({
	          text: 'Loading'
	        })
	        
	        if(this.loadingInterval){
	        	clearTimeout(this.loadingInterval)
	        }
	        
//	        this.loadingInterval = setTimeout(()=>{
//	        	this.hideLoading()	
//	        },3000)	        
	        
	    },
	    hideLoading () {
	    	setTimeout(() => {
	          this.$vux.loading.hide()
	        }, 800)
	    },
        handleRemove (file) {
            //  upload 
            const fileList = this.uploadList;
            this.uploadList.splice(fileList.indexOf(file), 1);
        },
        handleSuccess (res, file) {
        	let data = res.data
        	data.url = data.file_url
            //    list
            this.uploadList.push(res.data)
            this.hideLoading()
        },
        handleFormatError (file) {
        	this.hideLoading()
            this.alert(
                '',
                ' ' + file.name + '  jpg  png ',
                true
            );            
        },
        handleMaxSize (file) { 
        	this.hideLoading()
            this.alert(
                '',
                ' ' + file.name + '  10M',
                true
            );
        },
        handleBeforeUpload (postFiles,file) {        	
    		const check = this.uploadList.length < 10;
            if (!check) {                	
            	this.hideLoading()
                this.alert(
                	'',
                	' 9  ',
                	true
                );
            }else{
            	this.showLoading()
            }
            return check;
        },
        get_time_sections(bid,week_day,season){
            bid = bid||this.bid$[0]
            let time_sections = this.$store.state.gvars.time_sections
            let ts
            ts = time_sections.filter(item=>{
                return item.bid == bid && item.season == season && item.week_day == week_day
            })
            if(ts && ts.length > 0){
                return ts
            }
            ts = time_sections.filter(item=>{
                return item.bid == 0 && item.season == season && item.week_day == week_day
            })

            return ts
        },
        get_season_by_date(bid,dt){
    		bid  = bid||this.bid$[0]
            let int_day_start,int_day_end
            let year = dt.getFullYear()
            let int_day = util.int(moment(dt).format('YYYYMMDD'))
			
            let seasons = ['C','S','Q','H','A']
            let sd,season,result = 'A'
            //CSQH
            function year_replace(day,year){
                return day.replace('9999',year.toString())
            }
            
            for(let i=0;i<4;i++){
                season = seasons[i]
                sd = this.get_season_dates(bid,year,season)
                if(sd){
                    int_day_start = util.int(this.$filter('format_int_day')(year_replace(sd.int_day_start,year)))
                    int_day_end   = util.int(this.$filter('format_int_day')(year_replace(sd.int_day_end,year)))
                    if(int_day_start > int_day_end){
                        int_day_end = util.int_day_add_year(int_day_end,1)
                    }
                    if(int_day_start <= int_day && int_day_end >= int_day){
                        result = season
                        break;
                    }
                }
            }

            return result
        },
        get_season_dates(bid,year,season){
            let season_dates = this.$store.state.gvars.season_dates
            let sd = season_dates.find(item=>{
                return item.bid == bid && item.year == year && item.season == season
            })
            if(sd){
                return sd
            }
            sd = season_dates.find(item=>{
                return item.bid == 0 && item.year == year && item.season == season
            })
            if(sd){
                return sd
            }
            sd = season_dates.find(item=>{
                return item.bid == 0 && item.year == 0 && item.season == season
            })
            return sd
        },
    },
    computed: {
    	currentBid () {
			return this.$store.state.bids
		},
		currentBidText () {
			let bids = this.currentBid,
				result = util.sprintf('%s',this.$filter('branch_name')(bids[0]))
				
			if(bids.length>1){
				result = util.sprintf('%s%s',this.$filter('branch_name')(bids[0]),bids.length)						
			}
			return result
		},
    	isIOS () {
			let u = navigator.userAgent
			if(/(iPhone|iPad|iPod|iOS)/i.test(u)){
				return true
			}else{
				return false
			}
		},
    	showHeader () {
	  		const ua = navigator.userAgent.toLowerCase()
		    if(ua.match(/MicroMessenger/i)=="micromessenger") {  
		        return false
		    }   
	        return true
	  	},  
    	headerHeight () {
	  		if(this.showHeader){
	  			return 46
	  		}
	  		return 0
	  	},
    	user$ () {
    		return this.$store.state.user.info
    	},
    	eid$ () {
    		let employee = this.user$.employee
    		
    		if(!_.isEmpty(employee)){
    			return employee.eid
    		}
    		
    		return 0
    	},
    	defaultImg$ () {
    		return 'http://s1.xiao360.com/common_img/avatar.jpg'
    	},
    	bid$ () {
    		return this.$store.state.bids
    	}
    }
}


